export  class DateFormatter extends Date{
    constructor(dateStr, regStr = "MMDDYYYY") {
        super(dateStr);
        let matchYear = regStr.match(/Y+/);
        this.year = dateStr.toString().slice(
            matchYear.index,
            matchYear.index + matchYear[0].length
        );

        let matchDay = regStr.match(/D+/);
        this.day = dateStr.toString().slice(
            matchDay.index,
            matchDay.index + matchDay[0].length
        );

        let matchMonth = regStr.match(/M+/);
        this.month = dateStr.toString().slice(
            matchMonth.index,
            matchMonth.index + matchMonth[0].length
        );
    }
    getYear(){
        return parseInt(this.year);
    }
    getDay(){
        return parseInt(this.day);
    }
    getMonth(){
        return parseInt(this.month);
    }
    getStrMonth(){
        return this.month;
    }
    getFormattedMonth(){
        let month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
        'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        return month[parseInt(this.month - 1)];
    }
    getDate() {
        return `${this.getStrMonth()}-${this.getDay()}-${this.getYear()}`;
    }
    getFormattedDate() {
        return `${this.getFormattedMonth()} ${this.getDay()} ${this.getYear()}`;
    }
}
